function flip(classname) {
  let parent = document.getElementsByClassName(classname)[0]

  let upper = parent.children[0].children[0]
  let upperText = upper.children[0].children[0]
  let upperBack = parent.children[0].children[1]
  let upperBackText = upperBack.children[0].children[0]
  let lower = parent.children[1].children[0]
  let lowerText = lower.children[0].children[0]
  let lowerBack = parent.children[1].children[1]
  let lowerBackText = lowerBack.children[0].children[0]

  upper.style.transform = "rotateX(180deg)"
  lowerBack.style.transform = "rotateX(360deg)"

  setTimeout(function() {
    upper.style.transition = "transform 0s"
    lowerBack.style.transition = "transform 0s"
  }, 500)

  setTimeout(function () {
    upperText.innerText = upperText.innerText - 1
    lowerText.innerText = lowerText.innerText - 1
    upper.style.transform = "rotateX(0deg)"
    lowerBack.style.transform = "rotateX(180deg)"
  }, 600)

  setTimeout(function () {
    upper.style.transition = "transform 1s"
    lowerBack.style.transition = "transform 1s"
    upperBackText.innerText = upperBackText.innerText - 1
    lowerBackText.innerText = lowerBackText.innerText - 1
  }, 700)

}

setInterval(function() {flip("second")}, 1000)
setInterval(function() {flip("minute")}, 2000)
setInterval(function() {flip("hour")}, 3000)
setInterval(function() {flip("day")}, 4000)

